import React, { useState, useEffect, useRef } from 'react';
import '../css/DealReport.css'
import { useNavigate } from 'react-router-dom'; //  //  : 6 useNavigate .
import axios from 'axios';
import DealListComponent from './DealReportComponent/DealListComponent';
import Pagination from 'react-js-pagination' // npm i react-js-pagination 

/* 2022-12-12 admin user  */
export default function DealReport (){

    const [ pageInfo , setPageInfo ] = useState({  page : 1 , key : "" , keyword:""  }) // 1.    state
    const [ pageDto , setPageDto ] = useState( { list : [] } )// 1.   state
    // [ ] : array/list     {  } : object/dto
    // ------------------------------  1.   -------------------------------------- //
    function getboardlist( ){ // 2. server : pageInfo  => pageDto  [  : 1.  2. 3. 4.   --->   ]
        axios.post( "/bank/dealReport/boardlist" ,  pageInfo )
             .then( res => {
             console.log( "/bank/dealReport/boardlist " );
             console.log( "res.data" );
             console.log( res.data );
             setPageDto( res.data );
             })
             .catch( err => { console.log( err ); } )
    }
    useEffect( getboardlist , [ pageInfo ] )  // 3.    *** pageInfo  


    // ------------------------------  3.  -------------------------------------- //
    const onPage = ( page ) =>{
        setPageInfo(
            {   page : page ,
                key : pageInfo.key ,            //   
                keyword : pageInfo.keyword  }   //   
        )
    }
    // ------------------------------ --------------------------------------------------- //
    // ------------------------------  4.    -------------------------------------- //
    const onSearch = () => {
        setPageInfo(
            {   page : 1 ,              //    [ 1 ]
                key : document.querySelector('.key').value ,    //  
                keyword: document.querySelector('.keyword').value  } //  
        )
    }
    // --------------------------------------------------------------------- //
     const [ dealReportMainData , setDealReportMainData] = useState([])
     useEffect(()=>{
     axios.get( "http://localhost:8080/bank/dealReport") // 
                  .then( res => {
                          console.log("res.data?" + res.data);
                          console.log(res.data);
                          setDealReportMainData([...res.data])
                          console.log("axios dealReportMainData");
                          console.log(dealReportMainData);
         })// 
         axios.post("http://localhost:8080/bank/dealReport")


     },[])
         console.log("axios dealReportMainData");
         console.log(dealReportMainData);
         return(
             <div className="dealReport">
             <div className="dealReportComtent">
                 <div className="ContentText searchBox">
                     <span className="userNameOutput"> </span>
                     <span className="DefaultUserName"> </span>
                     <select className="key">
                         <option value="bhno"></option>
                         <option value="bcontent"></option>
                         <option value="bmoney"></option>
                         <option value="btypes"></option>
                         <option value="mname"></option>
                         <option value="acno"></option>
                         <option value="acno2"></option>
                         <option value="bacba"></option>
                         <option value="bacst"></option>
                     </select>
                     <input type="text" className="userNameinput keyword"/>
                     <button type="button" className="userButton" onClick={ onSearch }></button>
                 </div>
             </div>
             <DealListComponent props={dealReportMainData}/>
               <Pagination
                      activePage={ pageInfo.page  }
                      itemsCountPerPage = { 3 }
                      totalItemsCount = { pageDto.totalBoards }
                      pageRangeDisplayed = { 5 }
                      onChange={ onPage }
               />
         </div>
     );
}